import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FavoriteItemsComponent } from './pages/favorite-items/favorite-items.component';
import { HometownComponent } from './pages/hometown/hometown.component';
import { LandingComponent } from './pages/landing/landing.component';
import { MymapComponent } from './pages/mymap/mymap.component';
import { PersonalComponent } from './pages/personal/personal.component';


const routes: Routes = [
  { path: '', component: LandingComponent },
  { path: 'favoriteItems', component: FavoriteItemsComponent },
  { path: 'hometown', component: HometownComponent },
  { path: 'targetPerson', component: PersonalComponent },
  { path: 'myMap', component: MymapComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { relativeLinkResolution: 'legacy' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
